




































































import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import { Category } from "../structs/Category";
import { Statement } from "../structs/Statement";
import Axios from "axios";
import { Item } from "../structs/Item";

@Component
export default class EditFooter extends Vue {
  @Prop() category: Category | null = null;
  @Prop() statement: Statement | null = null;

  alphabet: string[] = "abcdefghijklmnopqrstuvwxyz1234567890".split("");
  async deleteItem(table: String, item: Item) {
    await Axios.delete("/delete/" + table + "/" + item.id);
    this.$emit("edited");
    
  }
  async save(table: String, category: Category) {
    await Axios.post("/update/" + table + "/" + category.id, category);
    this.$emit("edited");
  }
}
