

















import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import axios from "axios";

import { Statement } from "../structs/Statement";
import { Category } from "@/structs/Category";

@Component
export default class StatementsList extends Vue {
  @Prop() category: Category | null = null;

  statements: Statement[] = [];
  @Watch("category")
  async onCategoryChanged(value: Category, oldValue: Category) {
    this.update(value);
    return value;
  }
  async mounted() {}
  async update(value: Category) {
    this.statements = [];
    const { data } = await axios.get("//vanilin/statements?by=" + value.id);
    if (data.length == undefined) {
      return;
    }

    this.statements = data.map((el: any) => {
      return new Statement(el.id, el.title, el.keys, el.categoriesId);
    });
    return value;
  }
}
