











import { Component, Prop, Vue } from "vue-property-decorator";
import axios from "axios";

import { Category } from "../structs/Category";

@Component
export default class CategoriesList extends Vue {
  categories: Category[] = [];


   mounted() {
     this.load();
  }
  async load(){

    const { data } = await axios.get("//vanilin/categories");
    if (data.length == undefined) {
      return;
    }

    this.categories = data.map((el: any) => {
      return new Category(el.id, el.title, el.keys);
    });
  }
  
}
